ckground-color:#b9b9b9;border-style:none;box-shadow:inset -0.5px -0.5px 1px 0px #000,inset .5px .5px 1px 0px #fff;position:relative}#closeButton:after{position:absolute;bottom:2px;left:0;right:0;content:"Ã—";font-size:12px;font-weight:bold;color:#000;text-align:center}#screen{width:100%;justify-content:center}#aboutScreen{width:50%;flex-direction:column;align-items:center;margin-top:auto;margin-bottom:auto}#bottom{flex-direction:row}#description{width:50%;flex-direction:column;padding-right:30px;font-family:"Ubuntu Mono",monospace}#picture{width:50%;justify-content:center;align-content:center;background-color:#1d1d1d}#profilePicture{filter:blur(10px) grayscale(100%) opacity(1);transition:.4s}#profilePicture:hover{filter:none;transition:.4s}#list{display:flex;flex-direction:column;margin-top:25px;font-size:1.1rem}#mem{height:100%;width:100%;justify-content:center;align-items:center}#mem img{height:85%;width:85%;object-fit:contain}#skillsScreen{flex-direction:column;justify-content:center;align-items:center;width:100%;height:95%;margin:0px auto auto auto}#skills{flex-direction:column;flex-wrap:wrap;justify-content:center;align-content:center;width:100%;height:80%}#firstColumn,#secondColumn{display:flex;flex-direction:column;justify-content:center;align-content:space-between;margin-left:10%;margin-right:10%;width:50%}#skill{flex-direction:column;margin:30px 30px 20px 30px;position:relative;justify-content:center;align-items:center}#skill h2,h3{margin-top:10px;margin-bottom:10px;color:#22af22}#skillBar{width:300px;height:25px;box-shadow:0px 0px 0px 5px #22af22;align-items:center;flex-direction:row;justify-content:flex-start;padding:2.5px}#bars{position:absolute;width:300px;height:25px;background-image:linear-gradient(to right, #1d1d1d 0%, #1d1d1d 100%);animation:skill-bar 2s steps(20, end) 500ms forwards;z-index:2}#skillBar span{position:relative;width:calc(5% - 2.5px);height:100%;margin-right:2.5px;background-color:#22af22;z-index:1}#contactScreen{flex-direction:column;justify-content:center;align-content:space-around;margin:0px auto 0px auto}#email,#linkedin{flex-direction:column;justify-content:center;align-content:center;align-items:center;margin:70px 0 10px 0}#email h2,#linkedin h2{font-size:2rem;color:#22af22;margin:10px}#emailLink,#linkedin a{text-decoration:none;color:#22af22;font-family:"Ubuntu Mono",monospace;font-weight:300;font-size:1.3rem}#emailLink:hover,#linkedin a:hover{color:#187818}#underConstruction{justify-content:center;align-items:center;height:100%;width:100%}#underConstruction h1{overflow:hidden;border-right:.15em solid #22af22;white-space:nowrap;font-size:4rem;font-weight:900;color:#22af22;letter-spacing:-0.125rem;animation:typing 3.5s steps(40, end),blink-caret .75s step-end infinite}@media(max-width: 414px)and (orientation: portrait){#textBox{height:60%;margin:auto}#welcomeTextBox{min-height:70px}#welcomeText{word-wrap:break-word;white-space:normal;font-size:2rem;font-weight:700}#options{flex-direction:column;height:80%}#skillsBtn,#aboutBtn,#contactBtn,#projectsBtn{font-weight:300;font-size:1.3rem}#aboutScreen{width:100%}#bottom{flex-direction:column}#description{width:90%;margin:auto 5% auto 5%}#picture{width:100%}#profilePicture{width:200px;height:150px;object-fit:cover;object-position:100% 10%;filter:none}#list{margin-top:0px}#skillsScreen{width:100%}#skills{flex-direction:column;flex-wrap:nowrap;overflow-y:scroll;height:75vh;justify-content:flex-start;align-content:flex-start}#firstColumn,#secondColumn{margin:auto;width:100%;align-content:flex-start}}@keyframes typing{from{width:0}to{width:100%}}@keyframes blink-caret{from,to{border-color:rgba(0,0,0,0)}50%{border-color:#22af22}}@keyframes skill-bar{0%{background-image:linear-gradient(to right, #1d1d1d 0%, #1d1d1d 100%)}5%{background-image:linear-gradient(to right, transparent 0%, transparent 5%, #1d1d1d 5%, #1d1d1d 100%)}10%{background-image:linear-gradient(to right, transparent 0%, transparent 10%, #1d1d1d 10%, #1d1d1d 100%)}15%{background-image:linear-gradient(to right, transparent 0%, transparent 15%, #1d1d1d 15%, #1d1d1d 100%)}20%{background-image:linear-gradient(to right, transparent 0%, transparent 20%, #1d1d1d 20%, #1d1d1d 100%)}25%{background-image:linear-gradient(to right, transparent 0%, transparent 25%, #1d1d1d 25%, #1d1d1d 100%)}30%{background-image:linear-gradient(to right, transparent 0%, transparent 30%, #1d1d1d 30%, #1d1d1d 100%)}35%{background-image:linear-gradient(to right, transparent 0%, transparent 35%, #1d1d1d 35%, #1d1d1d 100%)}40%{background-image:linear-gradient(to right, transparent 0%, transparent 40%, #1d1d1d 40%, #1d1d1d 100%)}45%{background-image:linear-gradient(to right, transparent 0%, transparent 45%, #1d1d1d 45%, #1d1d1d 100%)}50%{background-image:linear-gradient(to right, transparent 0%, transparent 50%, #1d1d1d 50%, #1d1d1d 100%)}55%{background-image:linear-gradient(to right, transparent 0%, transparent 55%, #1d1d1d 55%, #1d1d1d 100%)}60%{background-image:linear-gradient(to right, transparent 0%, transparent 60%, #1d1d1d 60%, #1d1d1d 100%)}65%{background-image:linear-gradient(to right, transparent 0%, transparent 65%, #1d1d1d 65%, #1d1d1d 100%)}70%{background-image:linear-gradient(to right, transparent 0%, transparent 70%, #1d1d1d 70%, #1d1d1d 100%)}75%{background-image:linear-gradient(to right, transparent 0%, transparent 75%, #1d1d1d 75%, #1d1d1d 100%)}80%{background-image:linear-gradient(to right, transparent 0%, transparent 80%, #1d1d1d 80%, #1d1d1d 100%)}85%{background-image:linear-gradient(to right, transparent 0%, transparent 85%, #1d1d1d 85%, #1d1d1d 100%)}90%{background-image:linear-gradient(to right, transparent 0%, transparent 90%, #1d1d1d 90%, #1d1d1d 100%)}95%{background-image:linear-gradient(to right, transparent 0%, transparent 95%, #1d1d1d 95%, #1d1d1d 100%)}100%{background-image:linear-gradient(to right, transparent 0%, transparent 100%)}}